import React, { useState } from 'react'

import styles from './client-filter.module.css'
import checkIcon from '../../../assets/icons/Check.png'
import FilterButtonComponent from '../filter-button/filter-button.component'

function ClientFilterComponent(props) {
  const [allClients, selectAllClients] = useState(true)
  const [astro, selectAstro] = useState(false)
  const [itv, selectItv] = useState(false)
  const [mgm, selectMgm] = useState(false)
  const [turner, selectTurner] = useState(false)
  const [turner2, selectTurner2] = useState(false)
  const [selectedClients, setSelectedClients] = useState('')

  const selectClients = (client) => {
    if (client === 'AllClients') {
      alert(client)
    } else {
      setSelectedClients(selectedClients + ' ' + client)
    }
  }

  const selectBox = (selectedClient) => {
    switch (selectedClient) {
      case 'Astro':
        selectAstro(!astro)
        break
      case 'ITV':
        selectItv(!itv)
        break
      case 'MGM':
        selectMgm(!mgm)
        break
      case 'Turner':
        selectTurner(!turner)
        break
      case 'Turner2':
        selectTurner2(!turner2)
        break
      default:
        selectAllClients(!allClients)
        if (allClients) {
          selectAstro(true)
          selectItv(true)
          selectMgm(true)
          selectTurner(true)
          selectTurner2(true)
        } else {
          selectAstro(false)
          selectItv(false)
          selectMgm(false)
          selectTurner(false)
          selectTurner2(false)
        }
        break
    }
    selectClients(selectedClient)
  }

  return (
    <>
      <input
        className={styles.dropdownSearch}
        type="search"
        placeholder="Search"
      />
      <div className={styles.clientList}>
        <div style={{ height: '100px', overflowY: 'auto' }}>
          <div className={styles.item}>
            <div
              className={`${styles.checkbox} ${
                !allClients ? styles.selectedClient : null
              }`}
              style={{ marginRight: '10px' }}
              onClick={() => selectBox('AllClients')}
            >
              <img
                className={styles.checkIcon}
                src={checkIcon}
                alt="CheckIconAll"
              />
            </div>
            Select All
          </div>
          <div className={styles.item}>
            <div
              className={`${styles.checkbox} ${
                astro ? styles.selectedClient : null
              }`}
              style={{ marginRight: '10px' }}
              onClick={() => selectBox('Astro')}
            >
              <img
                className={styles.checkIcon}
                src={checkIcon}
                alt="CheckIcon1"
              />
            </div>
            Astro
          </div>
          <div className={styles.item}>
            <div
              className={`${styles.checkbox} ${
                itv ? styles.selectedClient : null
              }`}
              style={{ marginRight: '10px' }}
              onClick={() => selectBox('ITV')}
            >
              <img
                className={styles.checkIcon}
                src={checkIcon}
                alt="CheckIcon2"
              />
            </div>
            ITV
          </div>
          <div className={`${styles.item}`}>
            <div
              className={`${styles.checkbox} ${
                mgm ? styles.selectedClient : null
              }`}
              style={{ marginRight: '10px' }}
              onClick={() => selectBox('MGM')}
            >
              <img
                className={styles.checkIcon}
                src={checkIcon}
                alt="CheckIcon3"
              />
            </div>
            MGM
          </div>
          <div className={`${styles.item}`}>
            <div
              className={`${styles.checkbox} ${
                turner ? styles.selectedClient : null
              }`}
              style={{ marginRight: '10px' }}
              onClick={() => selectBox('Turner')}
            >
              <img
                className={styles.checkIcon}
                src={checkIcon}
                alt="CheckIcon4"
              />
            </div>
            Turner
          </div>
          <div className={`${styles.item}`}>
            <div
              className={`${styles.checkbox} ${
                turner2 ? styles.selectedClient : null
              }`}
              style={{ marginRight: '10px' }}
              onClick={() => selectBox('Turner2')}
            >
              <img
                className={styles.checkIcon}
                src={checkIcon}
                alt="CheckIcon5"
              />
            </div>
            Turner
          </div>
        </div>
      </div>
      <div className="px-4">
        <hr style={{ backgroundColor: '#6d6d73' }} />
      </div>
      <div className="d-flex">
        <p className={styles.clearFilterButton}>Clear</p>
        <FilterButtonComponent
          getData={props.getData}
          toggleDropdown={props.toggleDropdown}
          clients={selectedClients.replace(/\s+/g, '-').slice(1)}
        />
      </div>
    </>
  )
}

export default ClientFilterComponent
