import axios from 'axios'

class MonitoringService {
  getAllMonitorings = (
    pageNumber,
    pageSize,
    client,
    titleFilter,
    titleFilterValue,
    workerFilter,
    workerFilterValue,
    startDate,
    endDate,
    durationFilter,
    durationFilterValue,
    status
  ) => {
    const clientParam = client !== undefined ? '&client=' + client : ''
    const titleFilterParam =
      titleFilter !== undefined ? '&titleFilter=' + titleFilter : ''
    const titleFilterValueParam =
      titleFilterValue !== undefined
        ? '&titleFilterValue=' + titleFilterValue
        : ''
    const workerFilterParam =
      workerFilter !== undefined ? '&workerFilter=' + workerFilter : ''
    const workerFilterValueParam =
      workerFilterValue !== undefined
        ? '&workerFilterValue=' + workerFilterValue
        : ''
    const startDateParam =
      startDate !== undefined ? '&startDate=' + startDate : ''
    const endDateParam = endDate !== undefined ? '&endDate=' + endDate : ''
    const durationFilterParam =
      durationFilter !== undefined ? '&durationFilter=' + durationFilter : ''
    const durationFilterValueParam =
      durationFilterValue !== undefined
        ? '&durationFilterValue=' + durationFilterValue
        : ''
    const statusParam = status !== undefined ? '&status=' + status : ''

    const url =
      process.env.REACT_APP_HOST_URL +
      'monitoring/?pageNumber=' +
      pageNumber +
      '&pageSize=' +
      pageSize +
      clientParam +
      titleFilterParam +
      titleFilterValueParam +
      workerFilterParam +
      workerFilterValueParam +
      startDateParam +
      endDateParam +
      durationFilterParam +
      durationFilterValueParam +
      statusParam
    return axios.get(url)
  }
}
const monitoringService = new MonitoringService()
export default monitoringService
