import React, { useState, useEffect } from 'react'

import HeaderComponent from '../../shared/header/header.component'
import TableComponent from '../../shared/table/table.component'
import monitoringService from './monitoring.service'

function MonitoringComponent() {
  const [data, setData] = useState([])
  const [totalItems, setTotalItems] = useState(0)
  const [currentPage, setCurrentPage] = useState(0)
  const [pageSize, setPageSize] = useState(0)
  const [params, setParams] = useState({
    pageNumber: 1,
    pageSize: 12,
    client: undefined,
    titleFilter: undefined,
    titleFilterValue: undefined,
    workerFilter: undefined,
    workerFilterValue: undefined,
    startDate: undefined,
    endDate: undefined,
    durationFilter: undefined,
    durationFilterValue: undefined,
    statusFilter: undefined,
  })

  const [pageButtons, setPageButtons] = useState([
    {
      buttonText: 1,
    },
    {
      buttonText: 2,
    },
    {
      buttonText: 3,
    },
    {
      buttonText: 4,
    },
    {
      buttonText: 5,
    },
  ])

  const decrementPageButtonNumbers = () => {
    if (currentPage > 3) {
      setPageButtons([
        { buttonText: pageButtons[0].buttonText - 1 },
        { buttonText: pageButtons[1].buttonText - 1 },
        { buttonText: pageButtons[2].buttonText - 1 },
        { buttonText: pageButtons[3].buttonText - 1 },
        { buttonText: pageButtons[4].buttonText - 1 },
      ])
    }
  }

  const paginateTo = () => {
    if (currentPage > 3) {
      setPageButtons([
        { buttonText: currentPage - 1 },
        { buttonText: currentPage },
        { buttonText: currentPage + 1 },
        { buttonText: currentPage + 2 },
        { buttonText: currentPage + 3 },
      ])
    }
  }

  const incrementPageButtonNumbers = () => {
    if (currentPage > 2) {
      setPageButtons([
        { buttonText: pageButtons[0].buttonText + 1 },
        { buttonText: pageButtons[1].buttonText + 1 },
        { buttonText: pageButtons[2].buttonText + 1 },
        { buttonText: pageButtons[3].buttonText + 1 },
        { buttonText: pageButtons[4].buttonText + 1 },
      ])
    }
  }

  const getAllMonitorings = () => {
    setData([])
    setTotalItems(0)
    setPageSize(0)
    setCurrentPage(0)
    monitoringService
      .getAllMonitorings(
        params.pageNumber,
        params.pageSize,
        params.client,
        params.titleFilter,
        params.titleFilterValue,
        params.workerFilter,
        params.workerFilterValue,
        params.startDate,
        params.endDate,
        params.durationFilter,
        params.durationFilterValue,
        params.statusFilter
      )
      .then((res) => {
        setData(res.data.workerResultItems)
        setTotalItems(res.data.totalCount)
        setCurrentPage(res.data.currentPage)
        setPageSize(res.data.pageSize)
      })
      .catch((err) => {
        console.log(err)
      })
  }

  useEffect(() => {
    getAllMonitorings()
    return () => {}
  }, [])

  return (
    <>
      <HeaderComponent />
      <hr style={{ border: '1px inset #B1B1B1' }} />
      <TableComponent
        getData={getAllMonitorings}
        incrementPageButtonNumbers={incrementPageButtonNumbers}
        decrementPageButtonNumbers={decrementPageButtonNumbers}
        paginateTo={paginateTo}
        data={data}
        totalData={totalItems}
        currentPage={currentPage}
        pageSize={pageSize}
        pageButtons={pageButtons}
      />
    </>
  )
}

export default MonitoringComponent
