import React, { useState } from 'react'

import styles from './status-filter.module.css'
import checkIcon from '../../../assets/icons/Check.png'

function StatusFilterComponent() {
  const [selectAllStatus, setselectAllStatus] = useState(true)
  const [runningStatus, setRunningStatus] = useState(false)
  const [queuedStatus, setQueuedStatus] = useState(false)
  const [successStatus, setSuccessStatus] = useState(false)
  const [failedStatus, setFailedStatus] = useState(false)
  const [stoppedStatus, setStoppedStatus] = useState(false)

  const selectBox = (selectedStatus) => {
    switch (selectedStatus) {
      case 'Running':
        setRunningStatus(!runningStatus)
        break
      case 'Queued':
        setQueuedStatus(!queuedStatus)
        break
      case 'Success':
        setSuccessStatus(!successStatus)
        break
      case 'Failed':
        setFailedStatus(!failedStatus)
        break
      case 'Stopped':
        setStoppedStatus(!stoppedStatus)
        break
      default:
        setselectAllStatus(!selectAllStatus)
        if (selectAllStatus) {
          setRunningStatus(true)
          setQueuedStatus(true)
          setSuccessStatus(true)
          setFailedStatus(true)
          setStoppedStatus(true)
        } else {
          setRunningStatus(false)
          setQueuedStatus(false)
          setSuccessStatus(false)
          setFailedStatus(false)
          setStoppedStatus(false)
        }
        break
    }
  }
  return (
    <>
      <div className="p-3 py-2">
        <div></div>
        <div className={`${styles.item} d-flex mt-1`}>
          <div
            className={`${styles.checkbox} ${
              !selectAllStatus ? styles.selectedStatus : null
            }`}
            onClick={() => selectBox('Select All')}
          >
            <img
              className={styles.checkIcon}
              src={checkIcon}
              alt="CheckIconAll"
            />
          </div>
          <div className={styles.selectAllStatus}>
            <span>Select All</span>
          </div>
        </div>
        <div className={`${styles.item} d-flex`}>
          <div
            className={`${styles.checkbox} ${
              runningStatus ? styles.selectedStatus : null
            }`}
            onClick={() => selectBox('Running')}
          >
            <img
              className={styles.checkIcon}
              src={checkIcon}
              alt="CheckIcon1"
            />
          </div>
          <div className={styles.runningStatus}>
            <span>Running</span>
          </div>
        </div>
        <div className={`${styles.item} d-flex`}>
          <div
            className={`${styles.checkbox} ${
              queuedStatus ? styles.selectedStatus : null
            }`}
            onClick={() => selectBox('Queued')}
          >
            <img
              className={styles.checkIcon}
              src={checkIcon}
              alt="CheckIcon2"
            />
          </div>
          <div className={styles.queuedStatus}>
            <span>Queued</span>
          </div>
        </div>
        <div className={`${styles.item} d-flex`}>
          <div
            className={`${styles.checkbox} ${
              successStatus ? styles.selectedStatus : null
            }`}
            onClick={() => selectBox('Success')}
          >
            <img
              className={styles.checkIcon}
              src={checkIcon}
              alt="CheckIcon3"
            />
          </div>
          <div className={styles.successStatus}>
            <span>Success</span>
          </div>
        </div>
        <div className={`${styles.item} d-flex`}>
          <div
            className={`${styles.checkbox} ${
              failedStatus ? styles.selectedStatus : null
            }`}
            onClick={() => selectBox('Failed')}
          >
            <img
              className={styles.checkIcon}
              src={checkIcon}
              alt="CheckIcon4"
            />
          </div>
          <div className={styles.failedStatus}>
            <span>Failed</span>
          </div>
        </div>
        <div className={`${styles.item} d-flex`}>
          <div
            className={`${styles.checkbox} ${
              stoppedStatus ? styles.selectedStatus : null
            }`}
            onClick={() => selectBox('Stopped')}
          >
            <img
              className={styles.checkIcon}
              src={checkIcon}
              alt="CheckIcon5"
            />
          </div>
          <div className={styles.stoppedStatus}>
            <span>Stopped</span>
          </div>
        </div>

        <hr style={{ backgroundColor: '#6d6d73' }} />

        <div className="d-flex">
          <p className={styles.clearFilterButton}>Clear</p>
          <button className={styles.filterButton}>Filter</button>
        </div>
      </div>
    </>
  )
}

export default StatusFilterComponent
