import React, { useState } from 'react'
import styles from './table-body.module.css'

function TableBodyComponent(props) {
  const [selectedItem, setSelectedItem] = useState(0)

  const selectItem = (id) => {
    setSelectedItem(props.data[id].jobId)
    props.displayDataView(props.data[id])
  }

  return (
    <tbody
      className={`${styles.tbody} ${styles.tbodyDiv}`}
      style={{ overflowY: props.data.length > 12 ? 'auto' : null }}
    >
      {props.data.map((item, index) => {
        const successClassName =
          item.status === 'Success' ? styles.successStatus : null
        const runningClassName =
          item.status === 'Running' ? styles.runningStatus : null
        const failedClassName =
          item.status === 'Failed' ? styles.failedStatus : null
        const stoppedClassName =
          item.status === 'Stopped' ? styles.stoppedStatus : null
        const queuedClassName =
          item.status === 'Queued' ? styles.queuedStatus : null
        return (
          <tr
            className={
              selectedItem === item.jobId && props.activeDataView
                ? styles.activeRow
                : null
            }
            key={index}
            onClick={() => selectItem(index)}
            style={{ cursor: 'pointer' }}
          >
            <td className={styles.tdClient}>{item.client}</td>
            <td
              className={`${styles.tdTitle} ${
                props.activeDataView ? styles.activeDataView : null
              }`}
            >
              <div
                style={{
                  whiteSpace: 'nowrap',
                  overflow: 'hidden',
                  width: '100%',
                }}
              >
                {item.title}
              </div>
            </td>
            <td
              className={`${styles.tdWorker} ${
                props.activeDataView ? styles.activeDataView : null
              }`}
            >
              {item.worker}
            </td>
            <td
              className={`${styles.tdStarted} ${
                props.activeDataView ? styles.activeDataView : null
              }`}
            >
              {item.started}
            </td>
            <td
              className={`${styles.tdEnded} ${
                props.activeDataView ? styles.activeDataView : null
              }`}
            >
              {item.ended}
            </td>
            <td className={styles.tdDuration}>{item.duration}</td>
            <td className={styles.tdStatus}>
              <div
                className={`${successClassName} ${runningClassName} ${failedClassName} ${stoppedClassName} ${queuedClassName}`}
              >
                {item.status}
              </div>
            </td>
          </tr>
        )
      })}
    </tbody>
  )
}

export default TableBodyComponent
