import React from 'react'
import styles from './filter-button.module.css'

function FilterButtonComponent(props) {
  const handleFilter = () => {
    props.getData(
      1,
      12,
      props.clients,
      props.titleFilter,
      props.titleFilterValue
    )
    props.toggleDropdown(null)
  }
  return (
    <button className={styles.filterButton} onClick={handleFilter}>
      Filter
    </button>
  )
}

export default FilterButtonComponent
