import React, { useState } from 'react'
import styles from './duration-filter.module.css'

function DurationFilterComponent() {
  const [buttonText, setButtonText] = useState('Equal')
  const [showOptions, setShowOptions] = useState(false)

  const toggleOptions = () => {
    setShowOptions(!showOptions)
  }

  const selectOption = (text) => {
    setButtonText(text)
    setShowOptions(false)
  }

  return (
    <div className="p-3">
      <div></div>
      <div className="position-relative">
        <button className={styles.timeRangeButton} onClick={toggleOptions}>
          {buttonText}
        </button>
        <div
          className={styles.options}
          style={{ display: showOptions ? null : 'none' }}
        >
          <div
            className={styles.optionItem}
            onClick={() => selectOption('Equal')}
          >
            <span>Equal</span>
          </div>
          <div
            className={styles.optionItem}
            onClick={() => selectOption('Less than')}
          >
            <span>Less than</span>
          </div>
          <div
            className={styles.optionItem}
            onClick={() => selectOption('Greater than')}
          >
            <span>Greater than</span>
          </div>
        </div>
      </div>
      <br />
      <input className={styles.hoursInput} type="number" placeholder="HH" />
      <input className={styles.minutesInput} type="number" placeholder="MM" />
      <input className={styles.secondsInput} type="number" placeholder="SS" />
      <hr style={{ backgroundColor: '#6d6d73' }} />
      <div className="d-flex">
        <p className={styles.clearFilterButton}>Clear</p>
        <button className={styles.filterButton}>Filter</button>
      </div>
    </div>
  )
}

export default DurationFilterComponent
